import React, { useEffect, useState } from 'react'
import { Heading } from './ServicesElements';

const ViewBalance = (props) => {
    const usersList = props.usersList;
    const [balance, setBalance] = useState("");
    useEffect(()=>{
        console.log(usersList);
        let email = localStorage.getItem("user");
        let currentUser = usersList.filter( user => email === user.email );
        setBalance(currentUser[0].balance);
        console.log(email + " " + currentUser + " " + balance );
    }, []);
    return (
        <>
            <Heading>
                Your Current Balance Is : <span>Rs. {balance}</span>
            </Heading>
        </>
    )
}

export default ViewBalance;