import React from 'react'
import { Icon, Paragraph } from '../Header/HeaderElements';
import { AiOutlineFacebook, AiOutlineLinkedin } from 'react-icons/ai';
import {
    FooterContainer,
    Heading,
    ButtonSection,
    Button
} from "./FooterElements";

const Footer = () => {
    return (
        <FooterContainer id="footer">
            <Heading>Sheikh Zain Ul Abidin</Heading>
            <Paragraph>Software Engineer</Paragraph>
            <ButtonSection>
                <Button href="https://www.linkedin.com/in/zainsheikh98">
                    <Icon color={"blue"}icon={AiOutlineLinkedin}/>
                    <span>
                        <small>Connect With Me On</small><br/>LinkedIn
                    </span>
                </Button>
                <Button href="https://www.facebook.com/sheikh.zain.338">
                    <Icon color={"blue"}icon={AiOutlineFacebook}/>
                    <span>
                        <small>Connect With Me On</small><br/>FaceBook
                    </span>
                </Button>
            </ButtonSection>
        </FooterContainer>
    )
}

export default Footer;
