import React, { useState } from 'react';
import axios from "axios";
import { 
    Container,
    Heading,
    InputContainer,
    Input,
    Select,
    Button
 } from "../SignIn/SignInElements";
import {AiFillMail, AiFillDollarCircle} from "react-icons/ai";
import { Icon } from '../Header/HeaderElements';


const SendMoney = (props) => {

    let usersList = props.usersList;
    let senderEmail = localStorage.getItem("user");
    usersList = usersList.filter(user => user.email !== senderEmail);
    const [inputData, setInputData] = useState({
        recieverEmail : "",
        transferredAmount : ""
    });

    const handleChange = e=>{
        setInputData({
            ...inputData,
            [e.target.name]: e.target.value,
        })
    }

    const handleSubmit = e=>{
        e.preventDefault();
        const config = {
            headers:{
                'content-type': 'application/json'
            }
        }
        let recieverEmail = inputData.receiverEmail;
        let transferredAmount = inputData.transferredAmount;
        const body = JSON.stringify({senderEmail, recieverEmail, transferredAmount});
    
        try {
            axios.post("http://zainsheikh98.pythonanywhere.com/api/accounts/send/", body, config)
            .then(
                res => {
                    alert(res.data.message);
                    setInputData({
                        ...inputData,
                        receiverEmail: "",
                        transferredAmount: ""
                    })
                    window.location.reload();
                }
            )
        }
        catch (err) {
            alert("Something Went Wrong");
        }
    }

    return (
        <Container id="SendMoney">
            <Heading>
                <span>TSF. BANK</span><br/>Send Money
            </Heading>
            <form onSubmit={handleSubmit}>
                <InputContainer>
                    <Icon icon={AiFillMail} color={"rgb(6, 206, 106)"} size={"1rem"}/>
                    <Select auoComplete="off" placeholder="Enter Receiver Email" name="receiverEmail" required value={inputData.email} onChange={handleChange}>
                        {
                            usersList.map((user, ind)=>
                                (
                                    <option key={ind} value={user.email}>
                                        {user.email}
                                    </option>
                                )
                            )
                        }
                    </Select>
                </InputContainer>
                <InputContainer>
                    <Icon icon={AiFillDollarCircle} color={"rgb(6, 206, 106)"} size={"1rem"}/>
                    <Input auoComplete="off" placeholder="Enter Amount" type="number" name="transferredAmount" required value={inputData.transferredAmount} onChange={handleChange} />
                </InputContainer>
                <Button type="submit">Send</Button>
            </form>
        </Container>
    )
}

export default SendMoney;