import React from 'react'
import { Table, TableData, TableHead, TableRow } from '../TransactionHistory/TransactionHistoryElements';

const UsersList = (props) => {
    let usersList = props.usersList;
    return (
        <div>
            <Table>
                <TableRow>
                    <TableHead>
                        User ID.
                    </TableHead>
                    <TableHead>
                        Username
                    </TableHead>
                    <TableHead>
                        Email
                    </TableHead>
                    <TableHead>
                        Current Balance
                    </TableHead>
                </TableRow>
                {
                    usersList.map((user, ind) =>
                        (
                            <TableRow key={ind}>
                                <TableData>
                                    {user.id}
                                </TableData>
                                <TableData>
                                    {user.username}
                                </TableData>
                                <TableData>
                                    {user.email}
                                </TableData>
                                <TableData>
                                    {user.balance}
                                </TableData>
                            </TableRow>
                        )

                    )
                }
            </Table>
        </div>
    )
}

export default UsersList;
