import styled from "styled-components";
import {Link} from "react-scroll";
import {AiOutlineMenu} from "react-icons/ai"

export const NavbarContainer = styled.div`
    position: fixed;
    background-color: white;
    top: 0;
    font-size: 1rem;
    overflow: hidden; 
    width: 100%;
    height: ${ props => (props.isOpen? "auto" : "20vh")};
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition: all 0.5s ease-in-out;
    flex-wrap: wrap;
    z-index: 1000;
`;


export const Icon = props =>{
    const StyledIcon = styled(props.icon)`
        display: inline-flex;
        margin-right: 0.1rem;
        outline: none;
        color: rgb(6, 206, 106);
        font-size: 1rem;
        cursor: pointer;
    `;
    return <StyledIcon/>

}

export const Hamburger = styled(AiOutlineMenu)`
    display: none;
    outline: none;
    color: rgb(6, 206, 106);
    font-size: 2rem;
    margin: 1rem;
    cursor: pointer;

    @media all  and (max-width:900px){
        display: flex;
        flex-basis: 10%;
    };
`;

export const NavbarMenu = styled.div`
    display: flex;
    justify-content: space-between;

    @media all  and (max-width:900px){
        /* display: ${ props => (props.isOpen? "flex" : "none")}; */
        display: flex;
        transition: display 0.5s ease-out 1s;
        flex-direction: column;
        justify-content: flex-end;
        flex-wrap: wrap;
        overflow: hidden;
        transform: ${props => (props.isOpen? "translateY(0px)" : "translateY(-200vh)")};
        transition: transform 0.3s ease-in-out;
    };
`;

export const NavbarLogo = styled.h1`
    color: black;   
    margin: 1rem;
    span{
        color: rgb(6, 206, 106);

    }

    @media all  and (max-width:900px){
        flex-basis: 60%;
    };
`;
   

export const NavbarLink = styled(Link)`
    font-family: sans-serif;
    text-decoration: none;
    color: black;
    margin: 0.5rem;
    text-align: left;
    cursor: pointer;
    &:hover{
        color: rgb(6, 206, 106);
        font-weight: bold;
        transition: all 0.3s ease-out;
    }
    &.active{
        color: rgb(6, 206, 106);
        font-weight: bold;
        transition: all 0.3s ease-out;
    }
`;


export const LogoutButton = styled.button`
    display: flex;
    font-family: monospace;
    font-weight: 500;
    font-size: 1.3rem;
    background-color: maroon;
    color: white;
    border: 0.3rem solid white;
    border-radius: 10px;
    padding: 0.5rem;
    cursor: pointer;
`;

