import styled from "styled-components";



export const StoreContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    height: auto;
    width: 100vw;
    overflow: hidden;
`;


export const Heading = styled.h1`
    margin: 3rem 0 1rem 3rem;
    color: rgb(6, 206, 106);
    align-self: flex-start;

    span{
        color: black;
        text-decoration: underline;
        text-decoration-color: rgb(6, 206, 106);
    }
`;



export const Paragraph = styled.p`
    margin: 0 3rem;
    align-self: flex-start;
`;


export const CardSection = styled.section`
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    align-content: center;
`;



export const Card = styled.div`
    margin: 1rem;
    border: 1px solid rgba(6, 206, 106, 0.1);
    max-width: 300px;
    height: auto;
`;

export const ImageSection = styled.div`
    display: flex;
    justify-content: space-around;
    height: 200px;
    width: 100%;
`;

export const CardImage = styled.img`
    object-fit: contain;
    object-position: center;
    border-radius: 10%;
`;


export const CardHeading = styled.h5`
    padding: 0rem 1rem;
    text-align: left;
    text-transform: uppercase;
    font-weight: 400;
    font-family: sans-serif;
`;



export const CategorySection = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(0,0,0,0.1);
    border-top: none;
`;


export const Category = styled.p`
    color: rgba(0,0,0,0.5);
    margin-left: 0.5rem;
    font-size: 0.6rem;
`;


export const Rating = styled.div`
    display: flex;
    align-content: flex-start;
    align-items: flex-start;
`;

export const CardDetails = styled.div`
    display: flex;
    justify-content: center;
    align-items: baseline;
    font-size: 0.5rem;

    small{
        margin: 1rem;
        font-size: 0.5rem;
    }
`;


export const Stats = styled.div`
    border: 1px solid rgba(0,0,0,0.1);
    color: rgba(0,0,0,0.7);
    font-size: 0.5rem;
    text-align: center;
    padding: 1rem;
    font-family: sans-serif;

    span{
        font-weight: bold;
        font-size: 0.8rem;
        color: black;
    }
`;


export const SpecialOffers = styled.p`
    border-bottom: 1px solid rgba(0,0,0,0.1);
    padding: 0.5rem;
    font-family: sans-serif;
    font-weight: lighter;
    text-align: center;
`;


export const Button = styled.button`
    font-family: monospace;
    font-weight: 500;
    letter-spacing: 0.1rem;
    font-size: 1rem;
    background-color: black;
    color: white;
    border: 0.3rem solid white;
    border-radius: 10px;
    padding: 0.5rem;
    width: 90%;
    cursor: pointer;
    margin: 1rem;
`;




export const Table = styled.div`
    display: flex;
    width: 100%;
    align-content: center;
    justify-content: center;
    flex-direction: column;
`;
export const TableHead = styled.h6`
    word-wrap: break-word;
    margin: 0.5rem;
    width: 10vw;
`;
export const TableRow = styled.div`
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    text-align: center;
`;
export const TableData = styled.p`
    font-size: 0.7rem;
    text-align: center;
    width: 10vw;
    word-wrap: break-word;
`;