import styled from "styled-components";


export const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    min-height: 100vh;
    height: auto;
    width: 100vw;
    overflow: hidden;
`;

export const Heading = styled.h1`
    color: rgb(6, 206, 106);
    align-self: center;
    text-align: center;

    span{
        color: black;
        text-decoration: underline;
        text-decoration-color: rgb(6, 206, 106);
    }
`;

export const InputContainer = styled.div`
    display: flex;
    padding: 0.5rem;
    padding-bottom:0px;
    border: 1px solid rgb(6, 206, 106);
    border-radius: 1em;
    margin: 1rem 0px;
    width: 100%;
`;


export const Input = styled.input`
    border: none;
    outline: none;
    padding: 0.5rem;
    font-size: 0.7rem;
    background: white;
    color: black;
    width: 100%;
`;

export const Select = styled.select`
    border: none;
    outline: none;
    padding: 0.5rem;
    font-size: 0.7rem;
    background: white;
    color: black;
    width: 100%;
`;

export const Button = styled.button`
    font-family: monospace;
    font-weight: 500;
    letter-spacing: 0.1rem;
    font-size: 1rem;
    background-color: rgb(6, 206, 106);
    color: white;
    border: 0.3rem solid white;
    outline: none;
    border-radius: 10px;
    padding: 0.5rem;
    cursor: pointer;
    width: 100%;
    margin: 0.5rem;
`;