import React, { useState } from 'react';
import axios from "axios";
import { 
    Container,
    Heading,
    InputContainer,
    Input,
    Button
 } from "./SignInElements";

import {AiFillMail, AiFillLock} from "react-icons/ai";
import { Icon } from '../Header/HeaderElements';


const SignIn = () => {

    const [inputData, setInputData] = useState({
        email : "",
        password : ""
    });

    const handleChange = e=>{
        setInputData({
            ...inputData,
            [e.target.name]: e.target.value,
        })
    }

    const handleSubmit = e=>{
        e.preventDefault();
        const config = {
            headers:{
                'content-type': 'application/json'
            }
        }
        let email = inputData.email;
        let password = inputData.password;
        const body = JSON.stringify({email, password});
    
        try {
            axios.post("http://zainsheikh98.pythonanywhere.com/api/token/", body, config)
            .then(
                res => {
                    console.log(res.POST);
                    let accessToken = res.data.access;
                    if (accessToken !== ""){
                        localStorage.setItem("Token", accessToken);
                        localStorage.setItem("user", email);
                        localStorage.setItem("Authenticated", true);
                        alert("Login Successful");
                        window.location.reload();
                    }
                }
            )
        }
        catch (err) {
            alert("Invalid Username OR Password");
        }
    }

    return (
        <Container id="SignIn">
            <Heading>
                <span>TSF. BANK</span><br/>Sign.In
            </Heading>
            <form onSubmit={handleSubmit}>
                <InputContainer>
                    <Icon icon={AiFillMail} color={"rgb(6, 206, 106)"} size={"1rem"}/>
                    <Input auoComplete="off" placeholder="Enter Your Email" type="email" name="email" required value={inputData.email} onChange={handleChange} />
                </InputContainer>
                <InputContainer>
                    <Icon icon={AiFillLock} color={"rgb(6, 206, 106)"} size={"1rem"}/>
                    <Input auoComplete="off" placeholder="Enter Your password" type="password" name="password" required value={inputData.password} onChange={handleChange} />
                </InputContainer>
                <Button type="submit">Sign In</Button>
            </form>
        </Container>
    )
}

export default SignIn;