import styled from "styled-components";
import FooterImage from "../../media/footer.jpg";

export const FooterContainer = styled.footer`
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    overflow-x: hidden;
    background-image: linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.5)),url(${FooterImage});
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    height: auto;
    width: 100vw;
    overflow: hidden;
`;


export const Heading = styled.h1`
    margin-top: 40vh;
    color: white;
    text-align: center;
`;
export const ButtonSection = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
`;
export const Button = styled.a`
    text-decoration: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-family: monospace;
    font-size: 3rem;
    background-color: black;
    color: white;
    border: 0.1rem solid white;
    border-radius: 10px;
    padding: 1rem;
    margin:  1rem;
    width: 300px;
    cursor: pointer;
    span{
        font-size: 1rem;
        text-align: center;
    }
`;