import React from 'react'
import Money from "../../media/h1.jpg"
import Wallet from "../../media/h2.jpg"
import {
    HeaderContainer,
    Row,
    ImageWrapper,
    ImageText,
    Image,
    Heading,
    Paragraph,
} from "./HeaderElements";


const Header = () => {
    return (
        <HeaderContainer  id="home" >
            <Row>
                <ImageWrapper>
                    <ImageText>Money</ImageText>
                    <Image src={Money} alt="Money"/>
                </ImageWrapper>
                <Heading>The Sparks Foundation Bank</Heading>
                <ImageWrapper>
                    <ImageText>Wallet</ImageText>
                    <Image src={Wallet} alt="Wallet"/>
                </ImageWrapper>
            </Row>
            <Paragraph>Ea officia do laborum magna cupidatat ea culpa mollit sint adipisicing elit. Excepteur minim anim qui eu et. Reprehenderit voluptate amet cillum dolore sit consequat nostrud ipsum excepteur ad reprehenderit tempor exercitation ut.</Paragraph>
        </HeaderContainer>
    )
}

export default Header;
