import React, { useState } from 'react'
import {
    NavbarContainer,
    NavbarLogo,
    NavbarMenu,
    NavbarLink,
    Hamburger,
    LogoutButton
} from "./NavbarElements";



const Navbar = (props) => {
    const [isOpen, setIsOpen] = useState("false");
    const  menuToggler= ()=>{
        setIsOpen(!isOpen);
    };

    const logout = ()=>{
        localStorage.removeItem("user");
        localStorage.removeItem("Token");
        localStorage.removeItem("Authenticated");
        alert("Logout Successfully");
        window.location.reload();
    };

    return (
        <NavbarContainer isOpen={isOpen}>
            <NavbarLogo>
                TSF. <span>Bank</span>
            </NavbarLogo>
            <Hamburger isOpen={isOpen} onClick={menuToggler} />
            <NavbarMenu isOpen={isOpen}>
                <NavbarLink activeClassName="active" to="home"  spy={true} smooth={true} offset={-200} duration={500}>
                    Home
                </NavbarLink>
                <NavbarLink activeClassName="active" to="sendMoney"  spy={true} smooth={true} offset={-70} duration={500}>
                    Send Money
                </NavbarLink>
                <NavbarLink activeClassName="active" to="transactionHistory"  spy={true} smooth={true} offset={-70} duration={500}>
                    Transaction History
                </NavbarLink>
                <NavbarLink activeClassName="active" to="footer"  spy={true} smooth={true} offset={0} duration={500}>
                    About Developer
                </NavbarLink>
                <LogoutButton onClick={logout}>
                    Logout
                </LogoutButton>
            </NavbarMenu>
        </NavbarContainer>
    )
}

export default Navbar
