import React, { useEffect, useState } from 'react'
import {
    ServicesContainer,
    Heading,
    Paragraph,
    CardSection,
    Card,
    ImageSection,
    CardImage,
    CardHeading,
    CardDetails,
    Button,
    CloseButton,
    Modal
} from "./ServicesElements";
import customers from "../../media/customers.jpg";
import balance from "../../media/balance.jpg";
import transactionHistory from "../../media/transactionHistory.jpg";
import axios from "axios";
import SendMoney from './SendMoney';
import ViewBalance from './ViewBalance';
import UsersList from './UsersList';



const Services = () => {
    const [showBalance, setshowBalance] = useState(false);
    const [showSendMoneyForm, setshowSendMoneyForm] = useState(false);
    const [showUsersDetails, setShowUsersDetails] = useState(false);
    const [usersList, setUsersList] = useState([]);

    
    useEffect(()=>{
        try {
            axios.get("http://zainsheikh98.pythonanywhere.com/api/accounts/users/")
            .then(
                res => {
                      setUsersList(res.data);
                }
            )
          }
          catch (err) {
              alert("Error Rendering Data From Server");
          }
    }, []);

    const toggleShowBalance = ()=>{
        setshowBalance(!showBalance);
    }

    const toggleShowSendMoneyForm = ()=>{
        setshowSendMoneyForm(!showSendMoneyForm);
    }
    const toggleShowUsersDetails = ()=>{
        setShowUsersDetails(!showUsersDetails);
    }
    
    return (
        <ServicesContainer id="sendMoney">
            <Heading>
                <span>Send. </span>Money
            </Heading>
            <Paragraph>
                Choose From The Following Actions
            </Paragraph>
            <CardSection>
                <Card>
                    <ImageSection>
                        <CardImage src={transactionHistory} alt="users" />
                    </ImageSection>
                    <CardHeading>
                        Send Money
                    </CardHeading>
                    <CardDetails>
                        List Of All The Customers Of The Bank
                    </CardDetails>
                    <Button onClick={toggleShowSendMoneyForm}>Go</Button>
                </Card>
                <Card>
                    <ImageSection>
                        <CardImage src={customers} alt="Balance" />
                    </ImageSection>
                    <CardHeading>
                        Customers
                    </CardHeading>
                    <CardDetails>
                        View All Customers
                    </CardDetails>
                    <Button onClick={toggleShowUsersDetails}>Go</Button>
                </Card>
                <Card>
                    <ImageSection>
                        <CardImage src={balance} alt="Balance" />
                    </ImageSection>
                    <CardHeading>
                        Balance
                    </CardHeading>
                    <CardDetails>
                        Check Your Balance
                    </CardDetails>
                    <Button onClick={toggleShowBalance}>Go</Button>
                </Card>
            </CardSection>
            {   (showSendMoneyForm) &&
                <Modal display={showSendMoneyForm} >
                    <CloseButton onClick={toggleShowSendMoneyForm} />
                    <SendMoney usersList={usersList}/>
                </Modal>
            }
            {   (showUsersDetails) &&
                <Modal display={showUsersDetails} >
                    <CloseButton onClick={toggleShowUsersDetails} />
                    <UsersList usersList={usersList} />
                </Modal>
            }
            {
                (showBalance) &&
                <Modal display={showBalance} >
                    <CloseButton onClick={toggleShowBalance} />
                    <ViewBalance usersList={usersList}/>
                </Modal>
            }
        </ServicesContainer>
    )
}

export default Services;
