import React from 'react'
import {
    StoreContainer,
    Heading,
    Paragraph,
    Table,
    TableHead,
    TableRow,
    TableData

} from "./TransactionHistoryElements";
import { useEffect, useState } from "react";
import axios from "axios";

const TransactionHistory = () => {

    const [transactionHistory, setTransactionHistory] = useState([]);
    useEffect(()=>{
        try {
          axios.get("http://zainsheikh98.pythonanywhere.com/api/accounts/transactions/")
          .then(
              res => {
                    setTransactionHistory(res.data);
              }
          )
        }
        catch (err) {
            alert("Error Rendering Data From Server");
        }
      }, [])

    return (
        <StoreContainer id="transactionHistory">
            <Heading>
                <span>Transaction. </span>History
            </Heading>
            <Paragraph>
                Your Transaction History Is As Follows
            </Paragraph>
            <Table>
                <TableRow>
                    <TableHead>
                        Transaction ID.
                    </TableHead>
                    <TableHead>
                        SenderName
                    </TableHead>
                    <TableHead>
                        Reciever Name
                    </TableHead>
                    <TableHead>
                        Transferred Amount
                    </TableHead>
                </TableRow>
                {
                    transactionHistory.map((transaction, ind) =>
                        (
                            <TableRow key={ind}>
                                <TableData>
                                    {transaction.id}
                                </TableData>
                                <TableData>
                                    {transaction.sender.username}
                                </TableData>
                                <TableData>
                                    {transaction.reciever.username}
                                </TableData>
                                <TableData>
                                    {transaction.transferredAmount}
                                </TableData>
                            </TableRow>
                        )

                    )
                }
            </Table>
        </StoreContainer>
    )
}

export default TransactionHistory;



